  padding: 0;
  margin: 0;
  box-sizing: border-box;
  user-select: none;
}

:root {
  --bg-color: #113344;
  --header-color: floralwhite;
  --detail-color: white;
  --text-color: linear-gradient(
    to right,
    var(--heat-value) 0%,
    var(--cool-value) 100%
  );
  --secondary-color: rgba(255, 255, 255, 1);
  --cool-gradient: linear-gradient(to right, #30cfd0 0%, #330867 100%);
  --adaptive-color: rgba(255, 255, 255, 0);
  --bg-color: rgba(0, 0, 0, 0.3);
}

.pontano-sans-temp {
  font-family: "Pontano Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  background: var(--text-color);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

tinos-regular {
  font-family: "Tinos", serif;
  font-weight: 400;
  font-style: normal;
}

.tinos-bold {
  font-family: "Tinos", serif;
  font-weight: 700;
  font-style: normal;
}

.tinos-regular-italic {
  font-family: "Tinos", serif;
  font-weight: 400;
  font-style: italic;
}

.tinos-bold-italic {
  font-family: "Tinos", serif;
  font-weight: 700;
  font-style: italic;
}

.pontano-sans {
  font-family: "Pontano Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  color: var(--heat-value);
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-template-columns: 33% 77%;
}

#content-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: grid;
  grid-template-rows: 0.2fr 1.5fr 1fr 0.75fr;
}

#content-container > * {
  display: flex;
}

#header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--header-color);
  box-shadow: 0.01rem 0.01rem 0.5rem black;
}

#search-container {
  display: flex;
  width: 50%;
  justify-content: space-between;
  align-items: center;
  border: none;
  border-radius: 0.9rem;
  width: 1;
  background-color: white;
  border-radius: 3rem;
  box-shadow: 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.2);
  overflow: hidden;
}

#search-bar {
  display: flex;
  width: 80%;
  height: 100%;
  background-color: transparent;
  border: none;
  padding: 0.5rem;
  font-size: 0.9rem;
  border-right: 1px solid rgba(0, 0, 0, 0.3);
}

#search-bar:focus {
  display: flex;
  outline: none;
  border: none;
  border-right: 1px solid rgba(0, 0, 0, 0.3);
}

#search-btn {
  background-color: transparent;
  border: none;
  padding: 0.5rem;
  font-size: 0.9rem;
  width: 20%;
  height: 100%;
}

#search-btn:focus {
  background-color: transparent;
  border: none;
  padding: 0.5rem;
  font-size: 0.9rem;
  width: 20%;
  height: 100%;
  outline: none;
  border: none;
}

#main-container {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-self: center;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  width: 70%;
  height: 95%;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
  padding: 1rem;
  border: 2px solid black;
}

#main-weather-container {
  display: flex;
  flex-direction: row;
  justify-self: center;
  align-self: center;
  width: 100%;
  height: 100%;
}

#title-container {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  width: 2.5rem;
  border-radius: 1rem;
  height: 20%;
  background-color: var(--adaptive-color);
}

#title {
  text-shadow: 0.7rem 0.7rem 2.5rem black;
  color: var(--secondary-color);
  white-space: nowrap;
}

#left-main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%;
}

#main-temp-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 90%;
  height: 80%;
}

#main-icon-container {
  display: flex;
  justify-content: flex-end;
  justify-self: flex-start;
  width: 3rem;
  padding: 2.5rem;
}

#main-icon {
  display: flex;
  width: 100%;
  height: auto;
}

#current-temp-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 90%;
  border-radius: 1rem;
  background-color: var(--adaptive-color);
  margin: 0.5rem;
}

#main-temp {
  font-size: 7rem;
  text-shadow: 0.7rem 0.7rem 5rem black;
}

#main-max-min-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 90%;
}

#main-high,
#main-low {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3.5rem;
  border-radius: 1rem;
  width: 90%;
  height: 75%;
  margin: 0.5rem;
  background-color: var(--adaptive-color);
  text-shadow: 0.7rem 0.7rem 5rem black;
  font-style: italic;
}

#description-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  width: 50%;
  border-radius: 1rem;
  height: 20%;
  /* border: 1px solid black; */
  background-color: var(--adaptive-color);
}

#main-description {
  text-shadow: 0.7rem 0.7rem 2.5rem black;
  color: var(--secondary-color);
}

#right-main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
}

#weather-type-gif {
  display: flex;
  width: 25rem;
  height: auto;
  border-radius: 30px;
  box-shadow: 0.7rem 0.7rem 2.5rem;
}

#data-container {
  display: flex;
  justify-self: center;
  align-self: center;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90%;
  border: 1px transparent black;
  background-color: var(--header-color);
  box-shadow: 0.01rem 0.01rem 0.2rem black;
}

#left-detail-container,
#middle-detail-container,
#right-detail-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 100%;
  border-radius: 1rem;
  margin: 0.25rem;
}

.top-detail-container,
.bottom-detail-container,
.small-detail-container,
.large-detail-container {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
}

.top-detail-container > *,
.bottom-detail-container > *,
.small-detail-container > *,
.large-detail-container > * {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  text-align: center;
}

.header-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  padding: 0.3rem;
  border-radius: 0.9rem;
  padding: 1rem;
  font-size: 1.5rem;
  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.6);
  color: var(--detail-color);
}

.detail-container {
  margin: 0.25em;
  height: 90%;
  width: 90%;
  padding: 1rem;
}

.detail-container > * {
  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.5);
  color: var(--detail-color);
}

#horizontal-detail-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.small-detail-container {
  display: flex;
  flex-direction: column;
}

#rain-chance-container,
#gusts-container {
  width: 40%;
}

#right-container > .bottom-detail-container {
  display: flex;
  justify-self: flex-start;
  align-self: flex-start;
}

#gif-display {
  display: flex;
  justify-self: flex-end;
  align-self: center;
  height: 3rem;
  width: auto;
}

#bottom-container {
  display: flex;
  justify-self: center;
  align-self: center;
  justify-content: flex-start;
  align-items: center;
  width: 84.9%;
  height: 100%;
  overflow: hidden;
}

#future-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300%;
  height: 100%;
}

.next,
.previous {
  position: absolute;
  width: 2rem;
  height: auto;
  z-index: 10;
  border: 2px solid black;
  border-radius: 0.2rem;
}

.next {
  right: 8.3%;
  border-right: none;
}

.previous {
  left: 7.9%;
  border-left: none;
}

.future-high-low-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  font-style: italic;
}

.date-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  border: 1px solid black;
  width: 10%;
  height: 60%;
  margin: 0.5rem;
  padding: 0.25rem;
  background-color: var(--bg-color);
  box-shadow: 0.5rem 1rem 1rem rgba(0, 0, 0, 0.3);
}

.name-of-day {
  justify-self: flex-start;
  align-self: center;
  height: 30%;
  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.4);
  color: var(--secondary-color);
}

.future-data {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70%;
}

.future-data > span {
  color: red;
  font-size: 3rem;
  padding: 1rem;
}

.future-high-low-container > * {
  padding: 0.25rem;
  font-style: italic;
}

.future-data > * {
  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.2);
}

.hidden {
  display: none;
}

.sidebar {
  /* background-color: #409edf; */
  /* background-color: #a333f3; */
  background-color: #005f0f;
  font-family: sans-serif;
  box-sizing: border-box;
  color: #fff;
  grid-column: 1/2;
  display: grid;
  grid-template-rows: 10% 40% 40%;
  gap: 10px;
}

.sidebar > .logo,
.sidebar li {
  display: grid;
  grid-template-columns: 1fr 4fr;
  align-items: center;
  gap: 15px;
  transition-duration: 0.4s;
}

.sidebar svg {
  fill: #fff;
  grid-column: 1/2;
}
