ntent: center;\n  align-items: center;\n  grid-template-columns: 33% 77%;\n}\n\n#content-container {\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  display: grid;\n  grid-template-rows: 0.2fr 1.5fr 1fr 0.75fr;\n}\n\n#content-container > * {\n  display: flex;\n}\n\n#header {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background-color: var(--header-color);\n  box-shadow: 0.01rem 0.01rem 0.5rem black;\n}\n\n#search-container {\n  display: flex;\n  width: 50%;\n  justify-content: space-between;\n  align-items: center;\n  border: none;\n  border-radius: 0.9rem;\n  width: 1;\n  background-color: white;\n  border-radius: 3rem;\n  box-shadow: 0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.2);\n  overflow: hidden;\n}\n\n#search-bar {\n  display: flex;\n  width: 80%;\n  height: 100%;\n  background-color: transparent;\n  border: none;\n  padding: 0.5rem;\n  font-size: 0.9rem;\n  border-right: 1px solid rgba(0, 0, 0, 0.3);\n}\n\n#search-bar:focus {\n  display: flex;\n  outline: none;\n  border: none;\n  border-right: 1px solid rgba(0, 0, 0, 0.3);\n}\n\n#search-btn {\n  background-color: transparent;\n  border: none;\n  padding: 0.5rem;\n  font-size: 0.9rem;\n  width: 20%;\n  height: 100%;\n}\n\n#search-btn:focus {\n  background-color: transparent;\n  border: none;\n  padding: 0.5rem;\n  font-size: 0.9rem;\n  width: 20%;\n  height: 100%;\n  outline: none;\n  border: none;\n}\n\n#main-container {\n  display: flex;\n  flex-direction: column;\n  justify-self: center;\n  align-self: center;\n  justify-content: center;\n  align-items: center;\n  padding: 0.25rem;\n  width: 70%;\n  height: 95%;\n  background-color: rgba(0, 0, 0, 0.3);\n  border-radius: 1rem;\n  padding: 1rem;\n  border: 2px solid black;\n}\n\n#main-weather-container {\n  display: flex;\n  flex-direction: row;\n  justify-self: center;\n  align-self: center;\n  width: 100%;\n  height: 100%;\n}\n\n#title-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  font-size: 2.5rem;\n  width: 2.5rem;\n  border-radius: 1rem;\n  height: 20%;\n  background-color: var(--adaptive-color);\n}\n\n#title {\n  text-shadow: 0.7rem 0.7rem 2.5rem black;\n  color: var(--secondary-color);\n  white-space: nowrap;\n}\n\n#left-main-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 70%;\n}\n\n#main-temp-container {\n  display: flex;\n  justify-content: space-evenly;\n  align-items: center;\n  width: 90%;\n  height: 80%;\n}\n\n#main-icon-container {\n  display: flex;\n  justify-content: flex-end;\n  justify-self: flex-start;\n  width: 3rem;\n  padding: 2.5rem;\n}\n\n#main-icon {\n  display: flex;\n  width: 100%;\n  height: auto;\n}\n\n#current-temp-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 70%;\n  height: 90%;\n  border-radius: 1rem;\n  background-color: var(--adaptive-color);\n  margin: 0.5rem;\n}\n\n#main-temp {\n  font-size: 7rem;\n  text-shadow: 0.7rem 0.7rem 5rem black;\n}\n\n#main-max-min-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 30%;\n  height: 90%;\n}\n\n#main-high,\n#main-low {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  font-size: 3.5rem;\n  border-radius: 1rem;\n  width: 90%;\n  height: 75%;\n  margin: 0.5rem;\n  background-color: var(--adaptive-color);\n  text-shadow: 0.7rem 0.7rem 5rem black;\n  font-style: italic;\n}\n\n#description-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  font-size: 1.5rem;\n  width: 50%;\n  border-radius: 1rem;\n  height: 20%;\n  /* border: 1px solid black; */\n  background-color: var(--adaptive-color);\n}\n\n#main-description {\n  text-shadow: 0.7rem 0.7rem 2.5rem black;\n  color: var(--secondary-color);\n}\n\n#right-main-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 0.25rem;\n}\n\n#weather-type-gif {\n  display: flex;\n  width: 25rem;\n  height: auto;\n  border-radius: 30px;\n  box-shadow: 0.7rem 0.7rem 2.5rem;\n}\n\n#data-container {\n  display: flex;\n  justify-self: center;\n  align-self: center;\n  justify-content: center;\n  align-items: center;\n  width: 100%;\n  height: 90%;\n  border: 1px transparent black;\n  background-color: var(--header-color);\n  box-shadow: 0.01rem 0.01rem 0.2rem black;\n}\n\n#left-detail-container,\n#middle-detail-container,\n#right-detail-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 30%;\n  height: 100%;\n  border-radius: 1rem;\n  margin: 0.25rem;\n}\n\n.top-detail-container,\n.bottom-detail-container,\n.small-detail-container,\n.large-detail-container {\n  display: flex;\n  height: 100%;\n  width: 100%;\n  justify-content: space-evenly;\n  align-items: center;\n}\n\n.top-detail-container > *,\n.bottom-detail-container > *,\n.small-detail-container > *,\n.large-detail-container > * {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 100%;\n  height: 100%;\n  border-radius: 1rem;\n  text-align: center;\n}\n\n.header-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 30%;\n  padding: 0.3rem;\n  border-radius: 0.9rem;\n  padding: 1rem;\n  font-size: 1.5rem;\n  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.6);\n  color: var(--detail-color);\n}\n\n.detail-container {\n  margin: 0.25em;\n  height: 90%;\n  width: 90%;\n  padding: 1rem;\n}\n\n.detail-container > * {\n  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.5);\n  color: var(--detail-color);\n}\n\n#horizontal-detail-container {\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n  width: 100%;\n  height: 100%;\n}\n\n.small-detail-container {\n  display: flex;\n  flex-direction: column;\n}\n\n#rain-chance-container,\n#gusts-container {\n  width: 40%;\n}\n\n#right-container > .bottom-detail-container {\n  display: flex;\n  justify-self: flex-start;\n  align-self: flex-start;\n}\n\n#gif-display {\n  display: flex;\n  justify-self: flex-end;\n  align-self: center;\n  height: 3rem;\n  width: auto;\n}\n\n#bottom-container {\n  display: flex;\n  justify-self: center;\n  align-self: center;\n  justify-content: flex-start;\n  align-items: center;\n  width: 84.9%;\n  height: 100%;\n  overflow: hidden;\n}\n\n#future-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 300%;\n  height: 100%;\n}\n\n.next,\n.previous {\n  position: absolute;\n  width: 2rem;\n  height: auto;\n  z-index: 10;\n  border: 2px solid black;\n  border-radius: 0.2rem;\n}\n\n.next {\n  right: 8.3%;\n  border-right: none;\n}\n\n.previous {\n  left: 7.9%;\n  border-left: none;\n}\n\n.future-high-low-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-end;\n  align-items: flex-end;\n  font-style: italic;\n}\n\n.date-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  border-radius: 1rem;\n  border: 1px solid black;\n  width: 10%;\n  height: 60%;\n  margin: 0.5rem;\n  padding: 0.25rem;\n  background-color: var(--bg-color);\n  box-shadow: 0.5rem 1rem 1rem rgba(0, 0, 0, 0.3);\n}\n\n.name-of-day {\n  justify-self: flex-start;\n  align-self: center;\n  height: 30%;\n  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.4);\n  color: var(--secondary-color);\n}\n\n.future-data {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  height: 70%;\n}\n\n.future-data > span {\n  color: red;\n  font-size: 3rem;\n  padding: 1rem;\n}\n\n.future-high-low-container > * {\n  padding: 0.25rem;\n  font-style: italic;\n}\n\n.future-data > * {\n  text-shadow: 0.4rem 0.3rem 1rem rgba(0, 0, 0, 0.2);\n}\n\n.hidden {\n  display: none;\n}\n\n.sidebar {\n  /* background-color: #409edf; */\n  /* background-color: #a333f3; */\n  bac